import React from 'react';
import { NavLink } from 'react-router-dom';
import image from './buttons/about.jpg';




export const About = () => {
  return (
    <div>
        <div className="conntainer py-5 my-5">
            <div className="row">
                <div className="col-md-6">
                    <h1 className="text-primary-dark fw-bold mb-4">About Us</h1>
                    <p className="lead mb-4"> At Me'Nora, we believe there is a better way to do marketing. A more valuable, less invasive way where customers are earned rather than bought. We;re obsessively passionate about it, and our mission is to help people achieve it. We focus on search engine optimization (SEO). It's one of the least understood and least transparent aspects of great merketing, and we see that as and opportunity: We're excited to simplify SEO for everyone through our products and community.</p>
                    <NavLink to="/contact" className="btn btn-outline-dark px-3">Contact Us</NavLink>
                </div>
                <div className="col-md-6 d-flex justify-content-center">
                    <img src={image} alt="About Us" height="300px" width="500px"/>
                </div>
            </div>
        </div>
    </div>
  )
}
